import * as React from 'react';
import { navigate } from 'gatsby';
import { Theme } from '@material-ui/core/styles/createMuiTheme';
import createStyles from '@material-ui/core/styles/createStyles';
import withStyles, { WithStyles, StyleRules } from '@material-ui/core/styles/withStyles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Hidden from '@material-ui/core/Hidden';

// Drawer Components
import MapHandler from './drawer/MapHandler';
import SharesList from './drawer/SharesList';
import DrawerTable from './drawer/DrawerTable';

// Icons
import Clear from '@material-ui/icons/Clear';
import Reply from '@material-ui/icons/Reply';

import ValuesTable from '../components/ValuesTable';
import { Summary, AppState, VenueEdge } from '../utils/types';

const styles = (theme: Theme): StyleRules =>
  createStyles({
    root: {},
    toolbar: theme.mixins.toolbar
  });

interface Props extends WithStyles<typeof styles> {
  summary: Summary;
  appState: AppState;
  edges: VenueEdge[];
  handleDrawerToggle: () => any;
  handleZoomIn: () => any;
  handleZoomOut: () => any;
  handleTerrain: () => any;
  handlePopVisibility: () => any;
  handleBufferVisibility: () => any;
}

const DrawerInner: React.FunctionComponent<Props> = ({ classes, summary, appState, edges, handleDrawerToggle, ...mapHandler }: Props) => {
  const { mapState } = appState;

  const DrawerToolBar = () => (
    <div className={classes.toolbar}>
      <List>
        <Hidden mdUp implementation="css">
          <ListItem button onClick={handleDrawerToggle}>
            <ListItemIcon>
              <Clear />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </Hidden>
        <ListItem
          button
          onClick={() => {
            navigate('/', { state: appState });
          }}
        >
          <ListItemIcon>
            <Reply />
          </ListItemIcon>
          <ListItemText></ListItemText>
        </ListItem>
      </List>
    </div>
  );

  return (
    <div className={classes.root}>
      <DrawerToolBar />
      <ValuesTable summary={summary} />
      <MapHandler mapState={mapState} {...mapHandler} />
      <Divider />
      <SharesList summary={summary} />
      <Divider />
      <DrawerTable slug={summary.slug} edges={edges} appState={appState} />
    </div>
  );
};

export default withStyles(styles)(DrawerInner);
